<template>
  <div v-if="src && ratio" :class="containerClass" :style="computedContainerStyles">
    <div :class="imageClass" :style="computedImageStyles">
      <noscript>
        <img :src="src" :alt="alt">
      </noscript>
      <div v-if="$slots.default" :class="contentContainerClass">
        <slot></slot>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'vue-proper-image',
  data() {
    return {
      ratio: 0
    }
  },
  props: {
    src: {
      type: String,
      default: ''
    },
    alt: {
      type: String,
      default: ''
    },
    containerStyles: {
      type: Object,
      default: function() {
        return {}
      }
    },
    containerClass: {
      type: String,
      default: 'vue-proper-image-container'
    },
    contentContainerClass: {
      type: String,
      default: 'vue-proper-image-content'
    },
    imageStyles: {
      type: Object,
      default: function() {
        return {}
      }
    },
    imageClass: {
      type: String,
      default: 'vue-proper-image'
    },
    ratioWidth: {
      type: Number,
      default: 0
    },
    ratioHeight: {
      type: Number,
      default: 0
    }
  },
  computed: {
    computedContainerStyles() {
      if (!this.containerStyles) {
        this.containerStyles = {}
      }

      return this.containerStyles
    },
    computedImageStyles() {
      if (!this.imageStyles) {
        this.imageStyles = {}
      }

      if (this.src) {
        this.imageStyles.backgroundImage = "url('" + this.src + "')"
      }

      if (this.ratio) {
        this.imageStyles.paddingTop = this.ratio + '%'
      }

      return this.imageStyles
    }
  },
  created() {
    if (this.ratioHeight && this.ratioWidth) {
      this.ratio = this.ratioHeight / this.ratioWidth * 100
    } else {
      if (!this.ratio) {
        let instance = this
        let image = new Image()

        image.src = this.src

        image.onload = function() {
          instance.ratio = this.height / this.width * 100
        }
      }
    }
  }
}
</script>

<style>
.vue-proper-image-container {
  position: relative;
  overflow: hidden;
  width: 100%;
}

.vue-proper-image {
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  box-sizing: border-box;
  height: 0;
  width: 100%;
}

.vue-proper-image-content {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  padding: 20px;
}
</style>


